/*页面路由效果*/
.ConBox {width: 100%;height: 100vh; max-width:768px;position: absolute;top: 0;left: 150%;right: 0;margin: 0 -3.84rem;z-index: 2001;overflow-y: scroll; overflow-x:hidden;-webkit-transform-origin: 0px 0px;-webkit-transform: scale(1, 1);opacity: 1;background: #fff; }
.Jbox {width: 100%;background: white;}
.Jbox a {width: 100%;float:left;border-bottom: 1px solid #e8e8e8;text-align:center; height:1rem; line-height:1rem; position: relative; font-size:.3rem;}
.sidebarMove {height: 100%;overflow: hidden;animation: sidebar-body-move .5s ease;-webkit-animation: sidebar-body-move .5s ease;position: relative;left: -100%}
.sidebarBack {height: 100%;overflow: hidden; position: relative; animation: sidebar-body-back .50s ease;-webkit-animation: sidebar-body-back .50s ease; left: 0;}
.sidebarMove body {height: 100%;}
@keyframes sidebar-body-move {0% {left: 0}100% {left: -100%}}
@keyframes sidebar-body-back {0% {left: -100%}100% {left: 0}}
@-webkit-keyframes sidebar-body-move {0% {left: 0}100% {left: -100%}}
@-webkit-keyframes sidebar-body-back {0% {left: -100%}100% {left: 0}}

/* M站顶部 */
.top-header{ width:100%; max-width:768px; margin:0 auto; position: relative; background-color:#f6f6f6; line-height:.96rem; z-index:50; }
.top-header .top-back, .top-header .top-home{ position: absolute; left: 0; min-width:10%; height:.96rem; }
.top-header .top-back a, .top-header .top-home a{display:inline-block; min-width:.55rem; width:100%; height:.96rem; text-indent:-9999px; }
.top-header .top-home a {background: url(../images/home.png) .3rem center no-repeat; background-size: .51rem; }
.top-header .top-back a {background: url(../images/left.png) .3rem center no-repeat; background-size: .21rem; }
